<template>
  <div class="protected">
    <h2>Please input password to access this site.</h2>

    <form v-on:submit.prevent="validateBeforeSubmit">
      <div class="text-danger" v-if="error">
        <span>Incorrect password.</span>
      </div>
      <div class="form-group text-left">
        <Password class="password-field" name="password" promptLabel="Enter Password" toggleMask :feedback="false" variant="filled" v-model.trim="password" />
        <span class="error help-block"></span>
      </div>
      <Button class="btn p-button-success" type="submit" label="Submit" />
    </form>
  </div>
</template>

<script setup>
  import { ref } from 'vue';
  import { useRouter } from 'vue-router';

  const router = useRouter();
  
  const error = ref();
  const password = ref();

  function validateBeforeSubmit() {
    if (password.value === 'ldnl-chabadyouth') {
      error.value = false;
      localStorage.setItem('ldnl-sitepass', password.value);
      setTimeout(() => {
        router.push('/');
      }, 600);
    } else {
      error.value = true;
    }
  }
</script>

<style lang="scss" scoped>
  .protected {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    form {
      display: flex;
      flex-direction: column;
      gap: 1.5em;
      width: 100%;

      :deep(.p-password), :deep(.p-inputtext) {
        width: 100%;
      }
    }

    .text-danger {
      color: var(--red-500);
      text-align: left;
    }
  }
</style>
